import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { SharedService } from '../../shared/services/shared.service';
import { environment } from '../../../environments/environment';

@Component({
  selector: 'app-item-detail',
  templateUrl: './item-detail.component.html',
  styleUrls: ['./item-detail.component.css']
})
export class ItemDetailComponent implements OnInit {

    _host             = environment.config.BASE_URL;

	object:any      = {
        scheduler:[{startTime:'',closeTime:''}]
    };
   	products    = [];
    categories  = [];
    category_id = '';
    price       = ''
    reviews     = [];
    name        = '';
    sortBy      = '';
    pageReview  = 0;
    totalReview = 0;
    isFvorate   = false;

    rating      = null;
    existRating = false;
    review = {
        item_id:'',
        type:'dispensary',
        rating:null,
        detail:''
    }
    loginID     = '';
    productObj:any = {};

	constructor(
		private _route: ActivatedRoute,
    	private _sharedService: SharedService) { }

    ngOnInit() {
  		this.fetch();
        if(this._sharedService.isLogin()) {
          this.favorate();
          this.loginID = this._sharedService.loginID();
        }
        this.fetchProduct();
        this.fetchReviews();
         this.getAllCategories();
    }
  
    fetch() {
    	let options = {
    		id:this._route.snapshot.params['id']
    	}
        this.review['item_id'] = this._route.snapshot.params['id']
        this._sharedService.queryParams('itemDetail',options).then((res: any) => {
            if(res.success) {
                this.object = res.data.data[0]; 
                // this.products = res.data.key;
            } else {
            }
        },(error) => { });
    }

    addRemoveFavorate() {
        if(this._sharedService.isLogin()) {
            this._sharedService.loader('show');
            let obj = {
                item_id:this.object['id'],
                type:'dispensary'
              }
              this._sharedService.post(obj,'favourite').then((res: any) => {
            this._sharedService.loader('hide');
                  if(res.success) {
                      this.isFvorate = !this.isFvorate
                      this._sharedService.showAlert('Favourite Updated.','alert-success')
                  } else {
                  }
              },(error) => { 
                this._sharedService.loader('hide'); 
              });
        } else {
            this._sharedService.showAlert('Please Login to add in Favourite.','alert-danger')
        }
    }


    favorate() {
            let options = {
               id:this._route.snapshot.params['id']
            }
            this._sharedService.queryParams('itemFavorite',options).then((res: any) => {
                if(res.success) {
                  this.isFvorate = true;
                } else {
                }
            },(error) => { });
    }

    fetchProduct() {
        let options = {
               id:this._route.snapshot.params['id'],
               count:1000,
               page:1,
               sortBy:this.sortBy,
               name:this.name,
               category_id:this.category_id,
               price:this.price
        }
        this._sharedService.loader('show');
        this._sharedService.queryParams('ItemReviewsWithProduct',options).then((res: any) => {
            this._sharedService.loader('hide');
            if(res.success) {
                this.products = res.data.data;
            } else {
            }
        },(error) => { 
          this._sharedService.loader('hide');
        });
    }

    priceSelect() {
      setTimeout(()=>{
        this.fetchProduct();
      },1);
    }

    fetchReviews() {
        this.pageReview+=1;
        let options = {
               id:this._route.snapshot.params['id'],
               count:2,
               page:this.pageReview
        }
        this._sharedService.loader('show');
        this._sharedService.queryParams('itemreviews',options).then((res: any) => {
            this._sharedService.loader('hide');
            if(res.success) {
                res.data.products.forEach((obj)=>{
                    this.reviews.push(obj);
                    this.totalReview = res.data.total;
                })
            } else {
            }
        },(error) => { 
          this._sharedService.loader('hide');
        });
    }

    getAllCategories() {
      this._sharedService.get('categoryList').then(res => {
            if(res.success) {
                this.categories = res.data.category;
            } else {
            }
        },err => {  });
    }

    ratingR(rating) {
        if(rating) return parseInt(rating);
        else return 0;
    }

    clickStar( givenStar ) {
        this.rating = givenStar;
        this.review.rating = this.rating;
    }


    save() {
        if(this._sharedService.isLogin()) {
          this._sharedService.loader('show');
           this._sharedService.post(this.review,'reviews').then((res: any) => {
           this._sharedService.loader('hide');
                if(res.success) {
                    this.pageReview = 0;
                    this.fetchReviews();
                    this.review = {
                      item_id:this.object['id'],
                      type:'dispensary',
                      rating:null,
                      detail:''
                    }
                    this._sharedService.showAlert('Review Added Succefully.','alert-success')
                } else {
                    this._sharedService.showAlert(res.error.message,'alert-danger')
                }
            },(error) => { });
        } else {
                  this._sharedService.showAlert("Please Login to write a review.",'alert-danger')
        }
    }

    selectedProduct(obj) {
      this.productObj = obj;
    }

    productFavorate() {
        this._sharedService.loader('show');
        let obj = {
            product_id:this.productObj['product_id'],
            type:'product'
          }
          this._sharedService.post(obj,'favourite').then((res: any) => {
          this._sharedService.loader('hide');
                  this._sharedService.showAlert('Product Favorate Updated Successfuly.','alert-success')
          },(error) => { });
    }

}



